
<template>
    <div class="container" id="app">
      <div class="card text-center m-4">
        <div class="card-header text-left alert-warning">
          亲，你有{{count}}件事情要完成
          <input type="text" class="form-control mt-3" @keyup.enter="addItem" v-model="input">
        </div>
        <div class="card-body text-left">
          <h5 class="card-title"></h5>
          <ul class="list-group">
            <li class="list-group-item" v-for="(todo,index) in filterTodos" @dblclick="edit(todo)">
              <input type="checkbox" v-model="todo.isSelected">
              <!--如果isSelected为true则del生效-->
              <!--绑定class数据-->

              <!--当点击li时，可以知道点击的是哪一项,如果我点击的todo和当前循环的某一个相等的时候，应该显示输入框-->
              <span :class="{del:todo.isSelected}" v-show="cur!=todo">
                    {{todo.title}}
                </span>

              <!--blur失去焦点-->
              <!--v-show一定要放在最前面-->
              <input type="text" v-show="cur==todo" v-model="todo.title" v-focus="cur==todo" @keyup.enter="cancel" @blur="cancel">
              <button class="btn-sm btn-danger float-right" @click="rmvItem(index)"> remove </button>
            </li>
          </ul>
        </div>
        <div class="card-footer text-muted">
          <ul class="nav nav-pills">
            <li class="nav-item" >
              <a class="nav-link" :class="{active:hash=='all'}" href="#/all">全部任务</a>
            </li>
            <li class="nav-item" >
              <a class="nav-link" :class="{active:hash=='finish'}" href="#/finish">已完成</a>
            </li>
            <li class="nav-item" >
              <a class="nav-link" :class="{active:hash=='unfinish'}" href="#/unfinish">未完成</a>
            </li>
          </ul>
          <!--只要hash变化了-->


        </div>
      </div>

    </div>
</template>
<script src="/dist/build.js"></script>
<script>
  export default{
    data(){
      return {
        input:'',
        todos:[
          {isSelected:false,title:'sleep'},
          {isSelected:false,title:'dinner'},
        ],
        cur:'',
        hash:'',
      }
    },
    created(){ //ajax获取 初始化数据
      this.todos = JSON.parse(localStorage.getItem('data')) || this.todos
      // 监控hash的变化，如果页面已经有hash了，重新刷新野蛮也要获取hash值
      this.hash = window.location.hash.slice(2) || 'all'
      window.addEventListener('hashchange',()=>{
        // 当hash值变化 重新操作记录的数据
        this.hash = window.location.hash.slice(2);
      },false);

    },
    directives:{
      // 当点击li时，让内部的输入框获取焦点
      focus:{
        update:function(el,binding){
          if(binding.value){el.focus()}
        }
      }
    },
    watch:{
      //watch默认只监控一层的数据变化
      //也就是todos里某一个title变了就无法监控
      //希望深度监控
      todos:{
        handler(){ //默认写成函数 就相当于默认handler
          //localStorage默认存的是字符串
          localStorage.setItem('data',JSON.stringify(this.todos));
        },deep:true
      }
    },
    methods:{
      addItem(todo){
        this.todos.unshift({isSelected:false,title:this.input})
      },
      rmvItem(index){
        console.log(index);
        this.todos = this.todos.filter((item,i)=>index!=i)
      },
      edit(todo){
        //    当前传递的是todo属性，当前点击的这一项。
        this.cur = todo;
        console.log(this.cur);
      },
      cancel(){
        this.cur=''
        console.log(this.cur);
      }
    },
    computed:{
      filterTodos(){
        switch (this.hash){
          case 'finish':
            return this.todos.filter((item)=>item.isSelected);
            break;
          case 'unfinish':
            return this.todos.filter((item)=>!item.isSelected);
            break;
          default:
            return this.todos;
        }
      },
      count(){
        return this.todos.filter((item)=>!item.isSelected).length
      }},
  }
</script>
<style scoped>
  @import '../node_modules/bootstrap/dist/css/bootstrap.css';
</style>



